import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ApiProvider } from '../../providers/api/api';
import { LoadingController } from 'ionic-angular';
import { Events } from 'ionic-angular';
/**
 * Generated class for the EstadisticaPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */



@IonicPage()
@Component({
  selector: 'page-estadistica',
  templateUrl: 'estadistica.html',
})
export class EstadisticaPage {

  constructor(public navCtrl: NavController, public navParams: NavParams, public apiProvider: ApiProvider, public loadingController:LoadingController, public events: Events) {

  this.dataEstadistica={};

  
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad EstadisticaPage');
    this.cargarEstadistica();

  }

   cargarEstadistica() {
      this.apiProvider.verificarLogin()
    .then(data => {    
    this.apiProvider.getEstadistica(data.idUsuario)
      .then(data => {
     // this.proximoEjercicio = data[1][0];
     // this.diasRutina =  (Object.values(data[0]));
       console.log(data);
       this.dataEstadistica = data.data[0];
      // console.log(this.proximoEjercicio);
      });
     });

     }



}



